.portafolio__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
}
.portafolio__item {
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
.portafolio__item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}
.portafolio__item-image {
    border-radius: 1.5rem;
    overflow: hidden;
}
.portafolio__item h3 {
    margin: 1.2rem 0 2rem;
}

.portafolio__item-image img {
    width: 100%;
    height: 550px;
    object-fit: cover; 
    margin: auto;

  }
  .section-heading {
    margin-top: 3rem; /* o cualquier otro valor de margen */
  }


.portafolio__container > h2, .portafolio__container > h5 {
    text-align: center;
    color: var(--color-light);
}

.portafolio__container > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}
/* ================ MEDIA QUERRIES (MEDIUM DEVICES) ================*/
@media screen and (max-width: 1024px) {
    .portafolio__container {
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }
}

/* ================ MEDIA QUERRIES (SMALL DEVICES) ================*/
@media screen and (max-width: 600px) {
    .portafolio__container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
}